import { pluginName } from './constants';

const CKEDITOR = window.CKEDITOR;

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colors = CKEDITOR.tools.get_plugin_config(pluginName, editor);

    /**
     * Wrap TEXT node with cloned ancestor chain so that
     * <strong><em>txt</em></strong> + span ⇒ <span><strong><em>txt</em></strong></span>
     */
    function wrapInside(node, chain) {
      return chain.reduceRight((inner, outer) => {
        const clone = new CKEDITOR.dom.element(outer.getName());
        Object.entries(outer.getAttributes()).forEach(([k, v]) => clone.setAttribute(k, v));
        clone.append(inner);
        return clone;
      }, node);
    }

    /**
     * For every colour <span> that sits INSIDE an inline tag (strong/em/etc.)
     * clone that inline tag INSIDE the span so span wraps the clone.
     */
    function liftColorSpans(range) {
      const r = range.clone();
      r.enlarge(CKEDITOR.ENLARGE_INLINE);
      const walker = new CKEDITOR.dom.walker(r);
      walker.evaluator = n => n?.type === CKEDITOR.NODE_ELEMENT && n.getName() === 'span' && n.getStyle('color');

      let span;
      while ((span = walker.next())) {
        const parent = span.getParent();
        if (!parent || parent.getName() === 'span') continue; // already outer‑most
        if (span.getFirst() && span.getFirst().type === CKEDITOR.NODE_ELEMENT && span.getFirst().getName() === parent.getName()) continue; // clone exists

        console.log('🚀 Lift', span.getOuterHtml(), 'inside', parent.getName());

        // Clone inline parent (attrs only)
        const clone = new CKEDITOR.dom.element(parent.getName());
        Object.entries(parent.getAttributes()).forEach(([k, v]) => clone.setAttribute(k, v));

        // Move span's children into clone, then clone into span
        while (span.getFirst()) clone.append(span.getFirst().remove());
        span.append(clone);

        console.log('✅ After lift →', span.getOuterHtml());
      }
    }

    /** Remove colour but keep other inline styles */
    function removeColor(range) {
      const walker = new CKEDITOR.dom.walker(range.clone());
      walker.evaluator = n => n.type === CKEDITOR.NODE_TEXT && n.getAscendant('span', true)?.getStyle('color');

      let txt;
      while ((txt = walker.next())) {
        const colSpan = txt.getAscendant('span', true);
        if (!colSpan) continue;

        const start = txt.equals(range.startContainer) ? range.startOffset : 0;
        const end = txt.equals(range.endContainer) ? range.endOffset : txt.getLength();
        const [before, mid, after] = [txt.substring(0, start), txt.substring(start, end), txt.substring(end)];

        const chain = [];
        let p = txt.getParent();
        while (p && !p.equals(colSpan)) { chain.unshift(p); p = p.getParent(); }

        const makePart = (content, keepColor) => {
          if (!content) return null;
          const inside = wrapInside(new CKEDITOR.dom.text(content), chain);
          if (!keepColor) return inside;
          const s = new CKEDITOR.dom.element('span');
          s.setAttribute('style', colSpan.getAttribute('style'));
          s.append(inside);
          return s;
        };

        [makePart(before, true), wrapInside(new CKEDITOR.dom.text(mid), chain), makePart(after, true)]
          .filter(Boolean)
          .forEach(f => colSpan.insertBeforeMe(f));

        txt.remove();
        if (!colSpan.getChildCount()) colSpan.remove();
      }
    }

    /* ---------------- RichCombo ---------------- */
    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        Object.entries(colors).forEach(([label, val]) => this.add(val, label, label));
      },
      onRender() { this.setValue(''); },
      onClick(value) {
        editor.focus();
        let sel = editor.getSelection();
        if (!sel) return;
        let ranges = sel.getRanges();
        editor.fire('lockSnapshot');

        if (value === 'default') {
          console.log('🗑 Remove colour', ranges.length, 'range(s)');
          ranges.forEach(removeColor);
        } else {
          console.log('🎨 Apply colour', value, 'to', ranges.length, 'range(s)');
          const style = new CKEDITOR.style({ element: 'span', styles: { color: value } });
          editor.applyStyle(style);

          // CKEditor may split nodes; re‑grab ranges then lift spans
          sel = editor.getSelection();
          if (sel) {
            ranges = sel.getRanges();
            console.log('🔍 Lift colour spans in', ranges.length, 'range(s)');
            ranges.forEach(liftColorSpans);
          }
        }

        editor.fire('unlockSnapshot');
      }
    });
  }
});
